<template lang="pug">
div
  Header
  v-parallax(dark='', :src='backImage')
    v-layout(column='', justify-center='')
      h1.display-2.font-weight-thin.mb-3
  v-container(grid-list-xl)
    .headline.p20-top {{title}}
    v-layout.justify-center.stretch.p40(row, wrap)
      v-flex(md12='', xs12='')
        p.texture.p100-bot(v-html="description") {{description}}
</template>

<script>
import Header from '@/components/Header'
import bckImage from '@/assets/services/methodology.jpg'

export default {
  data () {
    return {
      backImage: bckImage,
      title: 'Careers',
      description: 'We are constantly looking for talented and skilled individuals to join our team. We provide you with excellent opportunities for professional development as well as competitive salaries. Please send your application to <a href="mailto:jobs@bestisoft.com">jobs@bestisoft.com</a>'
    }
  },
  components: {
    Header
  }
}
</script>



