import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/pages/Login'
import AdminHome from '@/pages/AdminHome'
// import Home from '@/pages/Home'
import FRONT from '@/pages/FRONT'
import Overview from '@/pages/aboutUs/Overview'
import News from '@/pages/aboutUs/News'
import Post from '@/pages/aboutUs/Post'
import Agilemethodology from '@/pages/solutions/AgileMethodology'
import Services from '@/pages/solutions/Services'
import WhyUs from '@/pages/solutions/WhyUs'
import Portfolio from '@/pages/solutions/Portfolio'
import Careers from '@/pages/Careers'
import Contact from '@/pages/Contact'
import helpers from '@/helpers'

Vue.use(Router)

function requireAuth (to, from, next) {
  const Cookie = helpers.getCookie('access_token')
  if (Cookie === '' || !Cookie) {
    next('/login')
  }
  next()
}

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: FRONT
    },
    {
      path: '/login',
      name: 'HelloWorld',
      component: Login
    },
    {
      path: '/admin',
      name: 'admin',
      component: AdminHome,
      beforeEnter: requireAuth
    },
    {
      path: '/aboutus/overview',
      name: 'Overview',
      component: Overview
    },
    {
      path: '/aboutus/news',
      name: 'News',
      component: News
    },
    {
      path: '/solutions/ourservices',
      name: 'Services',
      component: Services
    },
    {
      path: '/solutions/agilemethodology',
      name: 'AgileMethodology',
      component: Agilemethodology
    },
    {
      path: '/solutions/whyus',
      name: 'WhyUs',
      component: WhyUs
    },
    {
      path: '/solutions/portfolio',
      name: 'Portfolio',
      component: Portfolio
    },
    {
      path: '/careers',
      name: 'Careers',
      component: Careers
    },
    {
      path: '/contact',
      name: 'Contact',
      component: Contact
    },
    { path: '/aboutus/news/:id',
      name: 'Post',
      component: Post
    }
  ]
})



