<template lang="pug">
  section.theme-section.p60-top.p60-bot
    v-container(grid-list-xl)
      .headline.p20-bot News
      //- p.texture.p30-bot Lorem ipsum doler
      v-layout.justify-center.stretch(row, wrap)
        v-flex.my-2(md4='', sm4='', xs12='',  v-for='post in news' :key='post.id')
          v-card(hover color='white' height='100%', width='100%')
            v-img.p10(:src='post.url' height='300')
            v-card-title.layout.justify-center(primary-title='' style="display:block;")
              h3.headline.mb-0 {{post.title}}
            div.justify-left.m70-bot
              v-card-text.about-headline.p10-top {{post.description}}
              span(class='grey--text').fs10 {{post.date}}
            div.p15-bot(style='position: absolute; bottom: 0px; left: 50%; margin-left: -50px;')
              v-btn(flat outline color="blue" @click='goToPage(post.id)') Explore
      v-btn.blue.lighten-2.mt-5(dark='', large='', round, href='/aboutus/news') See More
</template>

<script>
import http from '@/instance'

export default {
  created () {
    this.initiateData()
  },
  data () {
    return {
      headline: 'News',
      news: null
    }
  },
  methods: {
    initiateData () {
      http.data.getLimitedData(0, 3)
        .then(response => {
          this.news = response.data.content
        })
    },
    goToPage (postId) {
      this.$router.push({ path: `aboutus/news/${postId}` })
    }
  }
}
</script>



