<template lang="pug">
div
  Header
  v-parallax(dark='', :src='backImage')
    v-layout(column='', justify-center='')
      h1.display-2.font-weight-thin.mb-3
  v-container(grid-list-xl)
    .headline {{title}}
    v-layout.justify-center.stretch.p40(row, wrap)
      v-flex(md12='', xs12='')
        p.texture.p30-bot {{description}}
</template>

<script>
import Header from '@/components/Header'
import bckImage from '@/assets/aboutUs/overview.jpg'

export default {
  data () {
    return {
      backImage: bckImage,
      title: 'About Us',
      description: 'Bestisoft was founded by experienced professionals. With dedicated teams, we have already helped many businesses reach their goals. We aim to deliver value to our clients by helping them implement and use technology in order to facilitate their business processes. Thus, it is our firm belief that our solutions always deliver superior value. We do so by making sure our solutions minimise business expenses, as well as automate business processes, to the fullest extent. We are highly flexible by having full-time employees, but also having a significant pool of talented candidates who can be utilised as needed. Our offices are located in Bitola, Macedonia.'
    }
  },
  components: {
    Header
  }
}
</script>



