<template lang="pug">
div
  Header
  v-parallax(dark='', :src='backImage')
    v-layout(column='', justify-center='')
      h1.display-2.font-weight-thin.mb-3
  v-container(grid-list-xl)
    .headline {{title}}
    v-layout.justify-center.stretch.p40(row, wrap)
      v-flex(md12='', xs12='')
        p.texture.p30-bot {{description}}
      v-flex.my-2(md4='', xs12='',  v-for='service in services' :key="service.id")
        v-card(hover flat color='white' height='100%', width='100%')
          v-icon.large.p20(color='#64b5f6') {{service.icon}}
          v-card-title.layout.justify-center(primary-title='' style="display:block;")
            .headline.text-xs-center {{service.title}}
          v-divider
          v-card-text.texture {{service.description}}
</template>

<script>
import Header from '@/components/Header'
import bckImage from '@/assets/services/whyUs.jpg'

export default {
  data () {
    return {
      backImage: bckImage,
      title: 'Why Choose Us',
      description: 'We always ensure that our clients’ needs are our utmost priority. With our perfectionist mentality we always strive to perform at the highest level. Being located in Southeast Europe we have low infrastructure and business costs, meaning we are highly cost efficient, and can deliver superior value to our clients. The main benefits you will enjoy by choosing us are:',
      services: [{
        id: 0,
        title: 'Quality',
        description: 'We strive to always deliver superior quality by working with highly skilled individuals',
        icon: 'thumb_up'
      }, {
        id: 1,
        title: 'Scalability',
        description: 'No matter the size, we are flexible enough to take on any project.',
        icon: 'trending_up'
      }, {
        id: 2,
        title: 'Business focus',
        description: 'With us taking care of business automation, you can focus on the core areas of your business',
        icon: 'work'
      }, {
        id: 3,
        title: 'Cost advantage',
        description: 'We make sure that our solutions are both affordable and create value',
        icon: 'attach_money'
      }, {
        id: 4,
        title: 'Speed',
        description: 'Our flexibility also allows us to efficiently manage time, enabling us to have short lead times',
        icon: 'hourglass_full'
      }]
    }
  },
  components: {
    Header
  }
}
</script>



