<template lang="pug">
div
  v-toolbar.white(fixed flat)
    v-img(:src="logo", contain, position="left", style="cursor: pointer;", @click='goHome()').wh65
    v-spacer
    v-toolbar-items.hidden-sm-and-down(v-for="menu in menus" :key='menu.id')
      v-menu(offset-y v-if="menu.children", open-on-hover )
        v-btn.menu-btn(slot="activator",flat, color="black", dark) {{menu.title}}
          v-icon(color='#64b5f6') arrow_drop_down
        v-list(v-for='child in menu.children' :key='child.id',dense )
          v-list-tile(@click="goToPage(menu.title, child.title)")
            v-list-tile-title {{child.title}}
      v-btn.menu-btn(flat, color="black", v-else, :key='menu.id' @click="goToPage(menu.title)") {{menu.title}}
    i.material-icons.hidden-md-and-up(@click.stop="drawer = !drawer") menu
    v-navigation-drawer.my-drawer(v-model="drawer" absolute height='400' temporary dark right app )
      v-list(v-for="menu in menus" :key='menu.id')
        v-list-tile(v-if='!menu.children')
            v-list-tile-action
              v-icon
            v-list-tile-title(@click="goToPage(menu.title)") {{menu.title}}
        v-list-group(prepend-icon='arrow_drop_down' v-else no-action='', sub-group='', value='true')
          v-list-tile(slot='activator')
            v-list-tile-title {{menu.title}}
          v-list-tile(v-for='child in menu.children' :key='child.id' @click='')
            v-list-tile-title(@click="goToPage(menu.title, child.title)" v-text='child.title')
</template>

<script>
import logo from '@/assets/bestisoft-blue.png'

export default {
  data () {
    return {
      logo: logo,
      title: 'Your Logo',
      drawer: null,
      menus: [{
        id: 0,
        title: 'About us',
        children: [{
          id: 0,
          title: 'Overview',
          children: false,
          active: true
        }, {
          id: 1,
          title: 'News',
          children: false
        }]
      }, {
        id: 1,
        title: 'Solutions',
        children: [{
          id: 0,
          title: 'Our Services',
          children: false
        }, {
          id: 1,
          title: 'Agile Methodology',
          children: false
        }, {
          id: 3,
          title: 'Why Us',
          children: false
        }, {
          id: 4,
          title: 'Portfolio',
          children: false
        }]
      }, {
        id: 2,
        title: 'Careers',
        children: false
      }, {
        id: 3,
        title: 'Contact',
        children: false
      }]
    }
  },
  methods: {
    goToPage (parent, page) {
      const parentPage = parent.replace(/\s/g, '').toLowerCase()
      if (page) {
        const childPage = page.replace(/\s/g, '').toLowerCase()
        this.$router.push({ path: `/${parentPage}/${childPage}` })
      } else {
        this.$router.push({ path: `/${parentPage}` })
      }
    },
    goHome () {
      this.$router.push({ path: '/' })
    },
    test (test) {

    }
  }
}
</script>

<style lang="scss" scoped>
  .test {
    background-color: transparent !important;
  }

  .my-drawer {
    max-height: 350px !important;
  }

  .test:hover {
    background-color: none !important;
  }

</style>



