<template lang="pug">
div
  Header
  GoogleMap
  v-container(grid-list-xl)
    .headline.p20-top.p10-bot {{title}}
    p.texture.p20-bot(v-html="description") {{description}}
    v-layout.stretch.p40(row, wrap, justify-space-between)
      v-flex(xs12, md6)
        v-form(xs12, md6 v-model='valid').w100
          h2.p10-bot Contact form
          v-flex(xs12='', md12='')
              v-text-field(v-model='email', :rules='emailRules', label='E-mail', required='')
          v-flex(xs12='', md12='')
              v-text-field(v-model='subject', :rules='nameRules', :counter='20', label='Subject', required='')
          v-flex(xs12 md12)
              v-textarea(v-model='message', :rules='messageRules', label='Message', required='')
          v-flex(justify-center)
            v-btn.blue.lighten-2.mt-5(@click='sendEmail', dark, large, round) Send
      v-flex(xs12, md4 offset-md1, d-block)
        v-flex(md12 xs12 d-block)
          h3.p10-bot Info
          div.w100(xs12 style='display: inline-flex;')
            i.material-icons.p10-side home
            p st. Solunska 224 - Bitola / Macedonia
          div.w100(style='display: inline-flex;')
            i.material-icons.p10-side mail
            p contact@bestisoft.com
          div.w100(style='display: inline-flex;')
            i.material-icons.p10-side contact_phone
            p +389 78 276 015
        v-flex(md12 xs12 d-block)
          h3.p10-bot Business Hours
          div.w100(xs12 style='display: inline-flex;')
            p.bold.p5-side Monday-Friday:
            p  08:00 to 16:00
          div.w100(xs12 style='display: inline-flex;')
            p.bold.p5-side Saturday:
            p  Close
          div.w100(xs12 style='display: inline-flex;')
            p.bold.p5-side Sunday:
            p  Close
        v-flex(md12 xs12 d-block)
          h3.p10-bot We can provide
          div.w100(xs12 style='display: inline-flex;')
            i.material-icons.p10-side check_box
            p Dedicated team formations
          div.w100(xs12 style='display: inline-flex;')
            i.material-icons.p10-side check_box
            p Software development
          div.w100(xs12 style='display: inline-flex;')
            i.material-icons.p10-side check_box
            p Technology consulting
</template>

<script>
import Header from '@/components/Header'
import bckImage from '@/assets/services/methodology.jpg'
import http from '@/instance'
import GoogleMap from '@/components/GoogleMap'

export default {
  data () {
    return {
      backImage: bckImage,
      title: 'Contact',
      description: 'Do you wish to discuss your business needs? Can we help you with software and web development?',
      valid: false,
      subject: '',
      message: '',
      messageRules: [
        v => !!v || 'Message is required'
      ],
      nameRules: [
        v => !!v || 'Subject is required',
        v => v.length <= 20 || 'Subject must be less than 20 characters'
      ],
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+/.test(v) || 'E-mail must be valid'
      ]
    }
  },
  methods: {
    sendEmail () {
      const data = {
        from: this.email,
        subject: this.subject,
        text: this.message
      }
      if (this.email.length > 1 && this.subject.length > 1 && this.message.length > 1) {
        http.data.sendEmail(data)
          .then(response => console.log('ok'))
      } else {
        console.error('check all fields')
      }
    }
  },
  components: {
    Header,
    GoogleMap
  }
}
</script>



