<template lang="pug">
  section.p0.m0
    v-fade-transition()
      v-layout.justify-center(row, wrap='', align-center)
        v-flex.my-3.p10(xs12, sm5, md5, lg3, offset-lg1, offset-md1, offset-sm1,)
          .text-xs-justify
            .headline.p30-bot Why Us
            h2.about-headline {{headline}}
            v-btn.blue.lighten-2.mt-5(dark='', large='', round, href='/solutions/whyus') See More
        v-flex(xs12, sm4, md4, lg4, offset-lg1, offset-md1, offset-sm1)
          v-img(:src='image')
</template>

<script>
import image from '@/assets/Design-1.png'
export default {
  data () {
    return {
      image: image,
      headline: 'We will take care of your technology needs by making your business goals our top priority. We have team of perfectionist\'s that will put your business ahead of the competition by offering efficient solutions and eye-catching designs. We are located in South Eastern Europe utilizing low infrastructure and business costs, which gives us an edge in cost efficacy and let us pass the value on to our clients.'
    }
  }
}
</script>



