<template lang="pug">
  section.theme-section.p60-top.p60-bot
    v-container(grid-list-xl)
      .headline.p20-bot Services
      //- p.texture.p30-bot Lorem ipsum doler
      v-layout.justify-center.stretch(row, wrap)
        v-flex.my-2(md3='', sm4='', xs12='',  v-for='service in services' :key="service.id")
          v-card(hover flat color='white' height='100%', width='100%')
            v-icon.large.p20(color='#64b5f6') {{service.icon}}
            v-card-title.layout.justify-center(primary-title='' style="display:block;")
              .headline.text-xs-center {{service.title}}
            v-divider
            v-card-text.texture.m70-bot {{service.text}}
            div.p15-bot(style='position: absolute; bottom: 0px; left: 50%; margin-left: -50px;')
              v-btn(flat outline color="blue" href='/solutions/ourservices') Explore
</template>

<script>
export default {
  data () {
    return {
      services: [{
        id: 0,
        title: 'Business Goals',
        text: 'Your business goals always come first as our',
        icon: 'wifi_tethering'
      }, {
        id: 2,
        title: 'Scalability',
        text: 'We’re up for a task of any size: hire a dedicated team or just utilize resources as needed',
        icon: 'filter'
      }, {
        id: 3,
        title: 'Tech Expertise',
        text: 'Our developers are highly skilled with profound work experience',
        icon: 'code'
      }, {
        id: 4,
        title: 'Inspiring',
        text: 'We always look to inspire confidence and trust in our clients',
        icon: 'people'
      }]
    }
  }
}
</script>

<style lang="scss">
.stretch {
  align-items: stretch !important;
}

.self-str {
  align-self: stretch !important;
}
</style>



