<template lang="pug">
  div
    Header
    v-parallax(dark='', :src='backImage')
      v-layout(column='', justify-center='')
        h1.display-2.font-weight-thin.mb-3
    v-container(grid-list-xl)
      .headline {{title}}
      v-layout.justify-center.align-items.stretch.p40.flex-container(row, wrap   v-for='service in services' :key="service.id" :class='{ odd:  isEven(service.id) }')
        v-flex(md6='', xs12='')
          p.texture.p30-bot {{service.content}}
        v-flex.my-2(md6='', xs12='')
          v-img(:src='service.image')
</template>

<script>
import Header from '@/components/Header'
import bckImage from '@/assets/services/provider.jpg'
import catalog from '@/assets/services/portfolio/web-catalog1.png'
import videoStream from '@/assets/services/portfolio/video-streaming2.jpg'
import microPayment from '@/assets/services/portfolio/micro-payment1.png'

export default {
  data () {
    return {
      backImage: bckImage,
      title: 'Portfolio',
      services: [{
        id: 0,
        content: 'On behalf of our clients we worked on micro-payment transfer project to be used by big international companies in improving their brand loyalty and providing customers with a way to make small transfers to themselves, various charities and other organizations. We’ve utilized microservices backend with Java Spring Boot to develop the API for the project as well as a Software Development Kit to be used by the ecosystem we help create around the project. It included interfacing and integrating with various external systems such as PayPal',
        image: microPayment
      }, {
        id: 1,
        content: 'On behalf of our clients we have built a web application to be used by their clients - who are international companies with more than $5 billion revenue -  in improving their brand loyalty. Our work on this project included working with video streaming, encoding, AWS, MySql, Java as well as  well as JavaScript for developing the front-end of the project. The full project was completed ahead of schedule, on budget, and with quality that earned us the respect of our clients.',
        image: videoStream
      }, {
        id: 2,
        content: 'On behalf of our clients we developed the front end for a web based template designer and editor to be used in publishing catalogs for a specific industry. The project had to meet strict technical specifications and be in compliance with various industry rules and regulations. The project was completed on time and on budget, proving our excellence once more.',
        image: catalog
      }]
    }
  },
  methods: {
    isEven (number) {
      return number & 1
    }
  },
  components: {
    Header
  }
}
</script>
<style lang="scss" scoped>
.odd {
  flex-direction: row-reverse !important;
}
</style>



