<template lang="pug">
  div
    Header
    v-parallax(dark='', :src='data.url')
      v-layout(column='', justify-center='')
        h1.display-2.font-weight-thin.mb-3
    v-container(grid-list-xl)
      .headline {{data.title}}
      v-layout.justify-center.stretch.p40(row, wrap)
        v-flex(md12='', xs12='')
          span(class='grey--text').fs10 {{data.date}}
        v-flex(md12='', xs12='')
          p.texture.p30-bot {{data.content}}
</template>

<script>
import Header from '@/components/Header'
import http from '@/instance'

export default {
  created () {
    this.getPostById(this.$route.params.id)
  },
  data () {
    return {
      data: []
    }
  },
  methods: {
    getPostById (id) {
      http.data.getPostById(id)
        .then(response => {
          this.data = response.data
        })
    }
  },
  components: {
    Header
  }
}
</script>



