<template lang="pug">
   div.google-map(:id="mapName")
</template>

<script>
export default {
  name: 'google-map',
  props: ['name'],
  data: function () {
    return {
      mapName: this.name + '-map',
      markerCoordinates: [{
        latitude: 41.025592,
        longitude: 21.335567
      }],
      map: null,
      bounds: null,
      markers: []
    }
  },
  mounted: function () {
    this.bounds = new window.google.maps.LatLngBounds()
    const element = document.getElementById(this.mapName)
    const mapCentre = this.markerCoordinates[0]
    const options = {
      center: new window.google.maps.LatLng(mapCentre.latitude, mapCentre.longitude),
      zoom: 17
    }
    this.map = new window.google.maps.Map(element, options)
    this.markerCoordinates.forEach((coord) => {
      const position = new window.google.maps.LatLng(coord.latitude, coord.longitude)
      const marker = new window.google.maps.Marker({
        position,
        map: this.map
      })
      this.markers.push(marker)
    })
  }
}
</script>
<style scoped>
.google-map {
  width: 100%;
  height: 500px;
  margin: 0 auto;
  background: gray;
}
</style>



