<template lang="pug">
div
  Header
  v-parallax(dark='', :src='backImage')
    v-layout(column='', justify-center='')
      h1.display-2.font-weight-thin.mb-3
  v-container(grid-list-xl)
    .headline {{title}}
    v-layout.justify-center.stretch.p40(row, wrap)
      v-flex(md12='', xs12='')
        p.texture.p30-bot {{description}}
      v-flex.my-2(md4='', xs12='',  v-for='service in services' :key="service.id")
        v-card(hover flat color='white' height='100%', width='100%')
          v-icon.large.p20(color='#64b5f6') {{service.icon}}
          v-card-title.layout.justify-center(primary-title='' style="display:block;")
            .headline.text-xs-center {{service.title}}
          v-divider
          v-card-text.texture {{service.description}}
</template>

<script>
import Header from '@/components/Header'
import bckImage from '@/assets/services/provider.jpg'

export default {
  data () {
    return {
      backImage: bckImage,
      title: 'Services',
      description: 'In our projects we use an agile methodology in order to provide a deep level of engagement between the client and the development team. This means that there is great flexibility in order to properly determine the goals of the projects. Furthermore, the agile methodology allows us to make continuous improvements on the final product. The interaction that is encouraged between the client and the team ensures that results are delivered continuously by the development team. This also means that the client is fully informed of the progress made and has effective performance tracking. We are thus able to ensure a transparent development process, with our solutions having predictable costs and lead times. The highlights of our services are:',
      services: [{
        id: 0,
        title: 'Software development',
        description: 'We can develop various software solutions, including web, desktop and mobile applications',
        icon: 'important_devices'
      }, {
        id: 1,
        title: 'Dedicated team formation',
        description: 'If needed, you can hire a dedicated team which will work exclusively for you',
        icon: 'people'
      }, {
        id: 2,
        title: 'Tech consulting',
        description: 'We can also provide consulting services. We can analyse your goals and determine the best available solutions.',
        icon: 'question_answer'
      }]
    }
  },
  components: {
    Header
  }
}
</script>



