<template lang="pug">
div
  Header
  v-parallax(dark='', :src='backImage')
    v-layout(column='', justify-center='')
      h1.display-2.font-weight-thin.mb-3
  v-container(grid-list-xl)
    .headline {{title}}
    v-layout.justify-center.stretch.p40(row, wrap)
      v-flex(md12='', xs12='')
        p.texture.p30-bot {{description}}
</template>

<script>
import Header from '@/components/Header'
import bckImage from '@/assets/services/methodology.jpg'

export default {
  data () {
    return {
      backImage: bckImage,
      title: 'Methodology',
      description: 'We use agile methodology to develop projects which provides a deeper level of engagement between the client and the development team. This allows greater flexibility for our clients in defining goals for the project and continuous improvement of the final product. It also allows for greater client interaction and puts constant pressure on the development team for continuous results. We keep you informed on the progress and have effective performance tracking. This will deliver transparent process and solutions with predictable costs and turnaround times'
    }
  },
  components: {
    Header
  }
}
</script>



