<template lang="pug">
  section.white-section.m64-top
    v-parallax.my-parallax(:src='bckImage' style='background-position: left center;')
      v-layout.dark--text.front-layout.row-res(align-center='', )
        v-flex.p50(xs12 sm12 md5 offset-md1 justify-end d-block)
          v-carousel( hide-controls light delimiter-icon='' height='550')
            v-carousel-item(v-for='slide in slides' :key='slide.id')
              v-flex(justify-center d-block).p90-top
                h1.dark--text.mb-2.display-1.text-xs-center.jumbo.p10-bot
                  span #
                  | {{slide.title}}
                .subheading.mb-3.text-xs-center.p20-side.p20-top {{slide.content}}
              v-flex(justify-center)
                v-btn.blue.lighten-2.mt-5(dark='', large='', round, :href='slide.link')
                  | {{slide.button}}
</template>

<script>
import image from '@/assets/test1.png'
import bckImage from '@/assets/bckLapTop.png'

export default {
  data () {
    return {
      image: image,
      bckImage: bckImage,
      slides: [{
        id: 0,
        title: 'Worldwide',
        content: 'We provide web and application development services to clients from all around the globe.Take a look at our all of the services we offer.',
        button: 'Services',
        link: '/solutions/ourservices'
      }, {
        id: 1,
        title: 'Solutions',
        content: 'We always look to provide the best solution for you and your business. Always focusing on your business needs, we deliver results. See why we are the best solutions provider for your business.',
        button: 'See more',
        link: '/solutions/ourservices'
      }, {
        id: 2,
        title: 'Careers',
        content: 'Bestisoft is a great place to work at. We provide a pleasant working environment with many opportunities for personal development.',
        button: 'Careers',
        link: '/careers'
      }]
    }
  }
}
</script>

<style lang="scss" >
.white-section {
  background-color: #fff;
}

.v-parallax__content {
  color: inherit;
}

.v-carousel {
  box-shadow: none !important;
  height: 100% !important;
}
.v-carousel__controls {
  background: none !important;
}
.v-btn--icon {
  background-color:#64b5f6 !important;
}
.v-carousel__controls__item {
  color: rgb(59, 59, 255) !important;
}

</style>



