<template lang="pug">
  section.p0.m0
    v-fade-transition()
      v-layout.justify-center.m0(row, wrap='', align-center)
        v-flex(xs12, sm4, md4, lg4)
          v-img(:src='image')
        v-flex.my-3.p10(xs12, sm5, md5, lg3, offset-lg1, offset-md1, offset-sm1,)
          .text-xs-justify
            .headline.p30-bot Overview
            h2.about-headline {{headline}}
            v-btn.blue.lighten-2.mt-5(dark='', large='', round, href='/aboutus/overview') See More
</template>

<script>
import image from '@/assets/Design-2.png'
export default {
  data () {
    return {
      image: image,
      headline: 'Besisoft was formed with the core idea to help business around the world to save costs and improve efficiency of their procesess by developing desktop, web, and mobile technology solutions for them. It is this business mindset that drives our employees to do extraordinary work for our clients daily. The constant improvement we strive for is our passion and vision. You can move your business forward today with Bestisoft.'
    }
  }
}
</script>



